"use client";

import { useEffect, useMemo, useRef, useState } from "react";
import { useRouter } from "next/navigation";
import { createInsight, getSessionUser, listLeagues, type League } from "@/lib/store";

type LeagueOption = { slug: string; name: string };

export default function NewInsightPage() {
  const router = useRouter();
  const user = getSessionUser();

  const [title, setTitle] = useState("");
  const [body, setBody] = useState("");

  const [leagues, setLeagues] = useState<LeagueOption[]>([]);
  const [leagueSlug, setLeagueSlug] = useState<string>("");
  const [loadingLeagues, setLoadingLeagues] = useState(true);

  const [tags, setTags] = useState<string[]>([]);
  const [tagInput, setTagInput] = useState("");

  const [coverFile, setCoverFile] = useState<File | null>(null);
  const coverInputRef = useRef<HTMLInputElement | null>(null);

  const [submitting, setSubmitting] = useState(false);
  const [error, setError] = useState<string | null>(null);

  // ✅ 修复点：listLeagues 是 async，必须 await 后再 map
  useEffect(() => {
    (async () => {
      try {
        setLoadingLeagues(true);
        const data: League[] = await listLeagues();
        setLeagues(data.map((l) => ({ slug: l.slug, name: l.name })));
      } finally {
        setLoadingLeagues(false);
      }
    })();
  }, []);

  const leagueSelectDisabled = useMemo(
    () => submitting || loadingLeagues || leagues.length === 0,
    [submitting, loadingLeagues, leagues.length]
  );

  function addTag(raw: string) {
    const t = raw.trim();
    if (!t) return;
    if (t.length > 16) return; // 可按需调整
    if (tags.includes(t)) return;
    if (tags.length >= 5) return;
    setTags((prev) => [...prev, t]);
  }

  function removeTag(t: string) {
    setTags((prev) => prev.filter((x) => x !== t));
  }

  async function onSubmit() {
    if (!user) {
      alert("需要登录后才能发布洞见");
      router.push("/auth/login");
      return;
    }

    if (!title.trim()) {
      setError("标题不能为空");
      return;
    }
    if (!body.trim()) {
      setError("内容不能为空");
      return;
    }

    setSubmitting(true);
    setError(null);

    // 这里暂时只保存文本 + league_slug
    // 封面图/分析图你后续要存 Supabase Storage 或单独表，我也可以继续帮你接
    const res = await createInsight({
      title,
      body,
      league_slug: leagueSlug ? leagueSlug : undefined,
    });

    if (!res.ok) {
      setSubmitting(false);
      setError(res.error ?? "发布失败");
      return;
    }

    router.push("/");
  }

  function onCancel() {
    router.push("/");
  }

  return (
    <main
      className="min-h-screen"
      style={{
        background:
          "radial-gradient(1200px 600px at 30% 20%, rgba(255,180,60,0.12), transparent 60%), radial-gradient(900px 500px at 70% 30%, rgba(60,120,255,0.12), transparent 60%), linear-gradient(180deg, #070B14 0%, #050812 100%)",
      }}
    >
      <div className="mx-auto max-w-5xl px-6 py-12">
        <div className="mb-8">
          <h1 className="text-3xl font-semibold text-white tracking-tight">发布洞见</h1>
          <p className="mt-2 text-sm text-white/60">
            分享你的 Fantasy 洞察、策略与分析（支持 Markdown）。
          </p>
        </div>

        <div className="rounded-2xl border border-white/10 bg-white/[0.03] backdrop-blur-xl shadow-[0_30px_80px_rgba(0,0,0,0.45)]">
          <div className="p-6 sm:p-8 space-y-6">
            {/* Cover uploader */}
            <section className="space-y-2">
              <div className="text-sm font-medium text-white/80">封面图片（可选）</div>

              <input
                ref={coverInputRef}
                type="file"
                accept="image/*"
                className="hidden"
                onChange={(e) => {
                  const f = e.target.files?.[0] ?? null;
                  setCoverFile(f);
                }}
                disabled={submitting}
              />

              <button
                type="button"
                onClick={() => coverInputRef.current?.click()}
                disabled={submitting}
                className="w-full rounded-xl border border-dashed border-white/15 bg-white/[0.02] hover:bg-white/[0.04] transition px-4 py-10 text-center"
              >
                <div className="text-white/80 font-medium">
                  {coverFile ? `已选择：${coverFile.name}` : "点击上传封面图"}
                </div>
                <div className="mt-1 text-xs text-white/50">推荐尺寸 16:9，最大 5MB</div>
              </button>
            </section>

            {/* Title */}
            <section className="space-y-2">
              <div className="text-sm font-medium text-white/80">标题</div>
              <input
                value={title}
                onChange={(e) => setTitle(e.target.value)}
                disabled={submitting}
                placeholder="例如：为什么我在第6轮放弃了 Tatum"
                className="w-full rounded-xl border border-white/10 bg-white/[0.03] px-4 py-3 text-white placeholder:text-white/35 outline-none focus:border-white/25 focus:bg-white/[0.04]"
                maxLength={100}
              />
              <div className="text-xs text-white/40">{title.length}/100</div>
            </section>

            {/* Body */}
            <section className="space-y-2">
              <div className="text-sm font-medium text-white/80">内容</div>
              <textarea
                value={body}
                onChange={(e) => setBody(e.target.value)}
                disabled={submitting}
                placeholder="分享你的策略、分析或复盘…（支持 Markdown）"
                rows={9}
                className="w-full resize-y rounded-xl border border-white/10 bg-white/[0.03] px-4 py-3 text-white placeholder:text-white/35 outline-none focus:border-white/25 focus:bg-white/[0.04]"
              />
              <div className="text-xs text-white/40">支持使用 Markdown 格式</div>
            </section>

            {/* Analysis Images placeholder */}
            <section className="space-y-2">
              <div className="text-sm font-medium text-white/80">分析配图（最多 9 张）</div>
              <div className="grid grid-cols-2 sm:grid-cols-3 gap-3">
                <button
                  type="button"
                  disabled={submitting}
                  className="aspect-square rounded-xl border border-dashed border-white/15 bg-white/[0.02] hover:bg-white/[0.04] transition flex items-center justify-center text-white/60"
                  title="后续可接入上传"
                >
                  <span className="text-2xl">+</span>
                </button>
                {/* 你之后把已上传的图渲染成缩略图就行 */}
              </div>
            </section>

            {/* Tags */}
            <section className="space-y-2">
              <div className="text-sm font-medium text-white/80">标签（最多 5 个）</div>

              <div className="flex gap-2">
                <input
                  value={tagInput}
                  onChange={(e) => setTagInput(e.target.value)}
                  disabled={submitting}
                  placeholder="输入标签后回车或点添加"
                  className="flex-1 rounded-xl border border-white/10 bg-white/[0.03] px-4 py-3 text-white placeholder:text-white/35 outline-none focus:border-white/25 focus:bg-white/[0.04]"
                  onKeyDown={(e) => {
                    if (e.key === "Enter") {
                      e.preventDefault();
                      addTag(tagInput);
                      setTagInput("");
                    }
                  }}
                />
                <button
                  type="button"
                  disabled={submitting}
                  onClick={() => {
                    addTag(tagInput);
                    setTagInput("");
                  }}
                  className="rounded-xl bg-[#F6A21A] px-4 py-3 text-black font-semibold hover:brightness-95 disabled:opacity-60"
                >
                  添加
                </button>
              </div>

              <div className="flex flex-wrap gap-2 pt-2">
                {tags.map((t) => (
                  <button
                    key={t}
                    type="button"
                    onClick={() => removeTag(t)}
                    className="rounded-full border border-white/10 bg-white/[0.04] px-3 py-1 text-xs text-white/80 hover:bg-white/[0.06]"
                    title="点击移除"
                  >
                    #{t} <span className="ml-1 text-white/40">×</span>
                  </button>
                ))}
                {tags.length === 0 && (
                  <div className="text-xs text-white/40">例如：#选秀策略 #球员分析 #交易建议</div>
                )}
              </div>
            </section>

            {/* League select */}
            <section className="space-y-2">
              <div className="text-sm font-medium text-white/80">关联联赛（可选）</div>
              <div className="relative">
                <select
                  value={leagueSlug}
                  onChange={(e) => setLeagueSlug(e.target.value)}
                  disabled={leagueSelectDisabled}
                  className="w-full appearance-none rounded-xl border border-white/10 bg-white/[0.03] px-4 py-3 pr-10 text-white outline-none focus:border-white/25 focus:bg-white/[0.04] disabled:opacity-60"
                >
                  <option value="">
                    {loadingLeagues
                      ? "加载中…"
                      : leagues.length === 0
                      ? "暂无联赛"
                      : "不关联联赛"}
                  </option>
                  {leagues.map((l) => (
                    <option key={l.slug} value={l.slug}>
                      {l.name}
                    </option>
                  ))}
                </select>
                <div className="pointer-events-none absolute right-3 top-1/2 -translate-y-1/2 text-white/50">
                  ▾
                </div>
              </div>
            </section>

            {/* Error */}
            {error && (
              <div className="rounded-xl border border-red-500/30 bg-red-500/10 px-4 py-3 text-sm text-red-200">
                {error}
              </div>
            )}

            {/* Actions */}
            <div className="flex items-center justify-end gap-3 pt-2">
              <button
                type="button"
                onClick={onCancel}
                disabled={submitting}
                className="rounded-xl border border-white/10 bg-white/[0.02] px-4 py-2.5 text-white/80 hover:bg-white/[0.04] disabled:opacity-60"
              >
                取消
              </button>
              <button
                type="button"
                onClick={onSubmit}
                disabled={submitting}
                className="rounded-xl bg-[#F6A21A] px-5 py-2.5 text-black font-semibold hover:brightness-95 disabled:opacity-60"
              >
                {submitting ? "发布中…" : "发布"}
              </button>
            </div>
          </div>
        </div>
      </div>
    </main>
  );
}
