// lib/store.ts
/* =========================================================
   Blueprint Fantasy — Domain Store with Supabase
   ========================================================= */

import { supabase } from "./supabase";
import { ALL_PLAYERS } from "./players-data";

// ==================== Types ====================

export type User = {
  id: string;
  name: string;
  email: string;
  username: string;
  avatar_url?: string;
};

export type League = {
  id: string;
  slug: string;
  name: string;
  owner_id: string;
  visibility: "public" | "private";
  created_at: string;
};

export type Insight = {
  id: string;
  title: string;
  body: string;
  league_slug?: string;
  author_id: string;
  author?: User;
  heat: number;
  created_at: string;
};

export type Comment = {
  id: string;
  insight_id: string;
  author_id: string;
  author?: User;
  body: string;
  created_at: string;
};

export type Player = {
  id: string;
  name: string;
  team: string;
  position: string;
  age: number;
  ppg: number;
  rpg: number;
  apg: number;
  spg: number;
  bpg: number;
  fg: number;
  ft: number;
  tov: number;
  gp: number;
  adp: number;
  rank: number;
  trend: "up" | "down" | "same";
  injury?: string;
};

// ==================== Session (localStorage for current user) ====================

const SESSION_KEY = "bp_session";

export function getSessionUser(): User | null {
  if (typeof window === "undefined") return null;
  const raw = localStorage.getItem(SESSION_KEY);
  if (!raw) return null;
  try {
    return JSON.parse(raw) as User;
  } catch {
    return null;
  }
}

function setSessionUser(user: User) {
  if (typeof window === "undefined") return;
  localStorage.setItem(SESSION_KEY, JSON.stringify(user));
}

export function logout() {
  if (typeof window === "undefined") return;
  localStorage.removeItem(SESSION_KEY);
}

// ==================== Auth (Supabase) ====================

export async function signup(name: string, email: string, password: string) {
  // Check if email already exists
  const { data: existingUser } = await supabase
    .from("users")
    .select("id")
    .eq("email", email)
    .single();

  if (existingUser) {
    return { ok: false as const, error: "Email already exists" };
  }

  // Create user in our users table
  const username = email.split("@")[0];
  const { data: newUser, error } = await supabase
    .from("users")
    .insert({
      name,
      email,
      username,
    })
    .select()
    .single();

  if (error) {
    return { ok: false as const, error: error.message };
  }

  // Store password hash in localStorage (简化版，生产环境应该用 Supabase Auth)
  const users = JSON.parse(localStorage.getItem("bp_users") || "[]");
  users.push({ id: newUser.id, email, password });
  localStorage.setItem("bp_users", JSON.stringify(users));

  setSessionUser(newUser);
  return { ok: true as const, user: newUser };
}

export async function login(email: string, password: string) {
  // Check password from localStorage
  const users = JSON.parse(localStorage.getItem("bp_users") || "[]");
  const storedUser = users.find((u: any) => u.email === email);
  
  if (!storedUser || storedUser.password !== password) {
    return { ok: false as const, error: "Invalid credentials" };
  }

  // Get full user data from Supabase
  const { data: user, error } = await supabase
    .from("users")
    .select("*")
    .eq("email", email)
    .single();

  if (error || !user) {
    return { ok: false as const, error: "User not found" };
  }

  setSessionUser(user);
  return { ok: true as const, user };
}

// ==================== Users ====================

export async function getUserById(id: string): Promise<User | null> {
  const { data, error } = await supabase
    .from("users")
    .select("*")
    .eq("id", id)
    .single();
  
  if (error) return null;
  return data;
}

export async function getUserByUsername(username: string): Promise<User | null> {
  const { data, error } = await supabase
    .from("users")
    .select("*")
    .eq("username", username)
    .single();
  
  if (error) return null;
  return data;
}

// ==================== Insights ====================

export async function listInsights(): Promise<Insight[]> {
  const { data, error } = await supabase
    .from("insights")
    .select(`
      *,
      author:users(id, name, username, avatar_url)
    `)
    .order("created_at", { ascending: false });

  if (error) {
    console.error("Error fetching insights:", error);
    return [];
  }
  return data || [];
}

export async function getInsightById(id: string): Promise<Insight | null> {
  const { data, error } = await supabase
    .from("insights")
    .select(`
      *,
      author:users(id, name, username, avatar_url)
    `)
    .eq("id", id)
    .single();

  if (error) return null;
  return data;
}

export async function createInsight(input: {
  title: string;
  body: string;
  league_slug?: string;
}) {
  const user = getSessionUser();
  if (!user) return { ok: false as const, error: "Login required" };

  const { data, error } = await supabase
    .from("insights")
    .insert({
      title: input.title.trim(),
      body: input.body.trim(),
      league_slug: input.league_slug,
      author_id: user.id,
      heat: Math.floor(80 + Math.random() * 200),
    })
    .select(`
      *,
      author:users(id, name, username, avatar_url)
    `)
    .single();

  if (error) {
    return { ok: false as const, error: error.message };
  }

  return { ok: true as const, insight: data };
}

// ==================== Comments ====================

export async function listComments(insightId: string): Promise<Comment[]> {
  const { data, error } = await supabase
    .from("comments")
    .select(`
      *,
      author:users(id, name, username, avatar_url)
    `)
    .eq("insight_id", insightId)
    .order("created_at", { ascending: true });

  if (error) return [];
  return data || [];
}

export async function addComment(insightId: string, body: string) {
  const user = getSessionUser();
  if (!user) return { ok: false as const, error: "Login required" };

  const { data, error } = await supabase
    .from("comments")
    .insert({
      insight_id: insightId,
      author_id: user.id,
      body: body.trim(),
    })
    .select(`
      *,
      author:users(id, name, username, avatar_url)
    `)
    .single();

  if (error) {
    return { ok: false as const, error: error.message };
  }

  return { ok: true as const, comment: data };
}

// ==================== Leagues ====================

export async function listLeagues(): Promise<League[]> {
  const { data, error } = await supabase
    .from("leagues")
    .select("*")
    .order("created_at", { ascending: false });

  if (error) return [];
  return data || [];
}

export async function getLeagueBySlug(slug: string): Promise<League | null> {
  const { data, error } = await supabase
    .from("leagues")
    .select("*")
    .eq("slug", slug)
    .single();

  if (error) return null;
  return data;
}

export async function createLeague(input: {
  name: string;
  visibility: "public" | "private";
}) {
  const user = getSessionUser();
  if (!user) return { ok: false as const, error: "Login required" };

  const slug = input.name
    .trim()
    .toLowerCase()
    .replace(/[^a-z0-9]+/g, "-")
    .replace(/^-+|-+$/g, "")
    .slice(0, 40);

  const { data, error } = await supabase
    .from("leagues")
    .insert({
      name: input.name.trim(),
      slug,
      owner_id: user.id,
      visibility: input.visibility,
    })
    .select()
    .single();

  if (error) {
    return { ok: false as const, error: error.message };
  }

  return { ok: true as const, league: data };
}

// ==================== Stats ====================

export async function getStats() {
  const [insightsRes, leaguesRes, usersRes] = await Promise.all([
    supabase.from("insights").select("id", { count: "exact", head: true }),
    supabase.from("leagues").select("id", { count: "exact", head: true }),
    supabase.from("users").select("id", { count: "exact", head: true }),
  ]);

  return {
    insightsCount: insightsRes.count || 0,
    leaguesCount: leaguesRes.count || 0,
    usersCount: usersRes.count || 0,
  };
}

// ==================== Players (still local) ====================

const DEFAULT_PLAYERS: Player[] = ALL_PLAYERS as Player[];

export function getPlayers(): Player[] {
  return DEFAULT_PLAYERS;
}

export function getPlayerById(id: string): Player | undefined {
  return DEFAULT_PLAYERS.find((p) => p.id === id);
}
